import React from "react";
import classes from './Deposit.module.scss'
import {Input} from "../../components/UI/Input/Input";
import {createAddDepositInputs, validateControl} from "../../Modules/formValidation"
import {Button} from "../../components/UI/Button/Button";
import {Preloader} from "../../components/Preloader/Preloader";
import {Title} from "../../components/Title/Title";
import {Modal} from "../../hoc/Modal/Modal";
import {FadeBg} from "../../hoc/Modal/FadeBg";
import axios from "../../axios/axios-settings";
import DepoInnerWrapper from "../DepoInnerWrapper/DepoInnerWrapper";
import DatePicker from "react-datepicker";
import "react-datepicker/dist/react-datepicker.css";

// Функции для генерации полей для инпутов

export default class Deposit extends React.Component {
	state = {
		key: "aHmcqjPPBwXvW397FPL31TtkwfJ3",
		isFormValid: false,
		showModal: false,
		showPreloader: true,
		deposit: {},
		formAddDepoInputs: {
			amount: {
				type: "text",
				errorMessage: "Введите число",
				className: ["depositInput"],
				placeholder: "Введите сумму",
				autocomplete: "off",
				valid: false,
				touched: false,
				value: "1",
				validation: {
					required: true,
					type: "number"
				}
			},
		},
		depositData: {
			currency: "usd",
			amount: "",
			date: new Date()
		},
		data: {}
	};

	handleChangeDate = date => {
		const depositData = {...this.state.depositData};
		depositData.date = date;
		this.setState({depositData});
	};
	selectHandler = (e) => {
		const depositData = {...this.state.depositData};
		depositData.currency = e.target.value;
		this.setState({depositData})
	};

	//TODO: Раскидать все в переменные по нормальному + настроить Datepicker (min max date и прочее)
	submitFormHandler = async (e) => {
		e.preventDefault();
		const {date, amount, currency} = this.state.depositData;
		const year = new Date(date).getFullYear();
		const arr = await axios.get(`/${this.state.key}/portfolio/deposit/${year}/${currency}.json`);
		const showPreloader = true;
		const formAddDepoInputs = {...this.state.formAddDepoInputs};
		const amountInput = {...formAddDepoInputs.amount};

		amountInput.value = "";
		formAddDepoInputs.amount = amountInput;

		// Если год создаем впервые
		if (arr.data === null) {
			const data = [];

			data.push({
				amount: +amount,
				date: date,
				id: data.length + Math.random()
			});

			const response = await axios.put(`/${this.state.key}/portfolio/deposit/${year}/${currency}.json`, data);
			const isFormValid = false;

			if (response.status === 200) {
				this.setState({showPreloader, isFormValid, formAddDepoInputs});
				this.renderDeposits();
			}

			return;
		}

		// Если уже имеем массив
		arr.data.push({
			amount: +amount,
			date: date,
			id: arr.data.length + Math.random()
		});

		const response = await axios.put(`/${this.state.key}/portfolio/deposit/${year}/${currency}.json`, arr.data);
		const isFormValid = false;

		if (response.status === 200) {
			this.setState({showPreloader, isFormValid, formAddDepoInputs});
			this.renderDeposits();
		}
	};
	onChangeHandler = (e, inputName, inputObj) => {
		const depositData = {...this.state.depositData};
		// Клонирую объект с инпутами
		const inputs = {...this.state[inputObj]};
		// controlName - amount, date
		const controlName = {...inputs[inputName]};

		// Валидация
		controlName.touched = true;
		controlName.value = e.target.value;
		controlName.valid = validateControl(controlName.value, controlName.validation);

		depositData[inputName] = e.target.value;
		inputs[inputName] = controlName;

		let isFormValid = true;

		// Валидация формы (активные кнопки или нет)
		Object.keys(inputs).forEach(name => {
			isFormValid = inputs[name].valid && isFormValid;
		});

		this.setState({
			depositData,
			[inputObj]: inputs,
			isFormValid
		});
	};
	deleteDeposit = async (e) => {
		const showPreloader = true;
		const showModal = false;
		const data = {...this.state.data};

		const response = await axios.get(`/${this.state.key}/portfolio/deposit/${data.year}/${data.currency}.json`);

		if (response.data.length === 0) {
			const del = await axios.delete(`/${this.state.key}/portfolio/deposit/${data.year}/${data.currency}.json`);

			if (del.status === 200) {
				this.setState({showPreloader, showModal});
				this.renderDeposits();
			}
		} else {
			const result = response.data.filter(item => item.id !== +data.id);
			const del = await axios.put(`/${this.state.key}/portfolio/deposit/${data.year}/${data.currency}.json`, result);

			if (del.status === 200) {
				this.setState({showPreloader, showModal});
				this.renderDeposits();
			}
		}
	};
	openDeleteModal = (e) => {
		const data = e.target.dataset;
		const showModal = true;

		this.setState({showModal, data});
	};
	closeDeleteModal = () => {
		this.setState({showModal: false})
	};

	renderInpus(inputName) {
		return Object.keys(this.state[inputName]).map((name, index) => {
			const inputObj = this.state[inputName][name];

			return (
				<Input
					key={name + index}
					type={inputObj.type}
					value={inputObj.value}
					classes={inputObj.className}
					htmlFor={name + index}
					valid={inputObj.valid}
					touched={inputObj.touched}
					label={inputObj.label}
					placeholder={inputObj.placeholder}
					errorMessage={inputObj.errorMessage}
					autocomplete={inputObj.autocomplete}
					onChange={e => this.onChangeHandler(e, name, inputName)}
				/>
			)
		})
	};

	async renderDeposits() {
		// TODO: создать общую переменную UserKey, которая будет определять пользователя
		try {
			// Ключ (ID пользователя)
			const userKey = this.state.key;
			// Импорт портфеля пользователя
			const response = await axios.get(`/${userKey}.json`);
			// Импорт компаний
			const {deposit} = response.data.portfolio;
			const showPreloader = false;

			if (response.status === 200) {
				this.setState({deposit, showPreloader})
			}
		} catch (e) {
			console.error(e)
		}
	}

	componentDidMount() {
		this.renderDeposits();
	}

	render() {
		return (
			<div className={classes.Deposit}>
				{/* Форма ввода пополнения */}
				<div>
					<Title title="Список пополнений"/>

					<h3 className={classes.infoTitle}>
						Добавить пополнение
					</h3>

					<form onSubmit={this.submitFormHandler}>
						<div className={classes.inputsBox}>
							{
								this.renderInpus("formAddDepoInputs")
							}
							<DatePicker
								selected={this.state.depositData.date}
								onChange={this.handleChangeDate}
								className={classes.datePicker}
								maxDate={new Date()}
							/>

							<select className={classes.select} onChange={this.selectHandler}>
								<option value="usd">USD</option>
								<option value="rub">RUB</option>
							</select>
						</div>

						<Button
							classses={["green", "addDepo"]}
							name="Добавить пополнение"
							disabled={this.state.isFormValid}
						/>
					</form>
				</div>

				{/*Общий контейнер для пополнения и статистики*/}
				<div className={classes.depoAndStatistic}>
					{/*Пополнения по годам*/}
					<h3 className={classes.infoTitle}>Общая информация по пополнениям</h3>
					{
						this.state.showPreloader
							? <Preloader/>
							: this.state.deposit === undefined
							? <div className={classes.noDeposit}> У вас пока не было пополнений </div>
							: <div className={classes.DepoYear}>
								{
									Object.keys(this.state.deposit).map((year, index) => {
										const currency = this.state.deposit[year];

										return <DepoInnerWrapper
											onClick={this.openDeleteModal}
											currency={currency}
											year={year}
											index={index}
											key={index}
										/>
									})
								}
							</div>
					}
				</div>

				{/*	Модальное окно для подтверждения удаления */}
				{
					this.state.showModal
						? <React.Fragment>
							<Modal>
								<h2 className={classes.delModalTitle}>Вы точно хотите удалить пополнение?</h2>
								<div className={classes.modalButtons}>
									<Button
										classses={["red"]}
										name="Удалить"
										disabled={true}
										onClick={this.deleteDeposit}
									/>
									<Button
										classses={["green"]}
										name="Отмена"
										disabled={true}
										onClick={this.closeDeleteModal}
									/>
								</div>
							</Modal>
							<FadeBg onClick={this.closeDeleteModal}/>
						</React.Fragment>
						: null
				}
			</div>
		)
	}
}